import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import { red } from "@material-ui/core/colors";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { useRef } from "react";
import { useTheme } from "@material-ui/core/styles";
import Link from "@material-ui/core/Link";
import { strippedString } from "../libs/string";

const cardThemeDark = createMuiTheme({
  palette: {
    type: "dark",
    background: {
      paper: "#505050",
      default: "#fff",
    },
  },
});
const cardThemeLight = createMuiTheme({
  palette: {
    type: "light",
    text: {
      primary: "#fff",
      secondary: "#fff",
    },
    background: {
      paper: "#0F1C24",
      default: "#fff",
    },
  },
});
const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 345,
    border: "1px solid #333",
    borderRadius: 0,
    "&:hover": {
      cursor: "pointer",
    },
  },
  media: {
    height: 140,
    //height: 0,
    //paddingTop: "56.25%", // 16:9
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: "rotate(180deg)",
  },
  avatar: {
    backgroundColor: red[500],
  },
  link: {
    color: theme.palette.text.primary,
    textDecoration: "none",
    "&:hover": {
      textDecoration: "none",
    },
  },
}));

export default function CardItem({
  pageNumber,
  image,
  category,
  url,
  title,
  description,
  content,
  excerptLength = 35,
}) {
  const classes = useStyles();
  const theme = useTheme();
  const imgRef = useRef();

  const currentTheme = () => {
    return theme.palette.type === "dark" ? cardThemeDark : cardThemeLight;
  };

  const shortTitle = (text) => {
    const stripped = strippedString(text);
    if (stripped.length > 31) {
      return stripped.slice(0, 31);
    } else {
      return text;
    }
  };

  return (
    <ThemeProvider theme={currentTheme()}>
      <Card variant="outlined" className={classes.root} title={description}>
        <Link href={url} target="_blank" className={classes.link}>
          <CardMedia
            ref={imgRef}
            component="img"
            className={classes.media}
            src={image || "/static/img/7.png"}
            title={description}
            onError={(e) => {
              console.log("error-img:" + image);
              if (imgRef.current) {
                imgRef.current.src = "/static/img/7.png";
              }
            }}
          />
          <CardContent>
            <Typography variant="body2" color="textSecondary">
              {shortTitle(description)}...
            </Typography>
          </CardContent>
        </Link>
      </Card>
    </ThemeProvider>
  );
}
