import React, { useState, useEffect } from "react";
import { news3d } from "../../../libs/news";
//import { Google } from "../../../libs/News/Google/Google";
import Card from "../../../components/Card";

import IconButton from "@material-ui/core/IconButton";
import Hidden from "@material-ui/core/Hidden";
import Box from "@material-ui/core/Box";
import ArrowBackIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIcon from "@material-ui/icons/ArrowForwardIos";
import Particles from "react-particles-js";
import Container from "@material-ui/core/Container";
import SimpleBreadcrumbs from "../../../layout/SimpleBreadcrumbs";
import Grid from "@material-ui/core/Grid";
import Skeleton from "@material-ui/lab/Skeleton";
import Footer from "../../../components/Footer";
import { getHeaderBackgroundColor } from "../../../utils/Theme";
import { useTheme } from "@material-ui/core/styles";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  root: {
    width: 300,
  },
  container: {
    display: "flex",
    flexDirection: "column",
    [theme.breakpoints.between("xs", "md")]: {
      minHeight: "calc(100vh - 131px)",
    },
    [theme.breakpoints.up("md")]: {
      minHeight: "calc(100vh - 225px)",
    },
  },
}));

function News() {
  const classes = useStyles();
  const [data, setData] = useState(null);
  const [page, setPage] = useState(1);
  const theme = useTheme();

  useEffect(() => {
    document.title = "Новости";
    news3d(setData);
    return () => {
      setData(null);
    };
  }, []);

  const getCards = () => {
    if (data !== null) {
      const partial = data.slice(page * 9 - 9, page * 9);

      return partial.map((item, index) => {
        return (
          <Grid key={index} item xs={12} sm={6} md={4}>
            <Card
              key={index}
              pageNumber={page}
              image={item.enclosure || null}
              category=""
              url={item.link}
              title={item.title}
              description={item.title}
            />
          </Grid>
        );
      });
    } else {
      return getSkeletons();
    }
  };

  const getSkeletons = () => {
    let items = [];
    for (let i = 0; i < 9; i++) {
      items[i] = (
        <Grid key={i} item xs={12} sm={4} style={{ maxWidth: 345 }}>
          <Box style={{ width: "100%" }} marginRight={0.5} my={1}>
            <Skeleton variant="rect" style={{ width: "100%" }} height={170} />
            <Box>
              <Skeleton />
              <Skeleton width="60%" />
            </Box>
          </Box>
        </Grid>
      );
    }
    return items;
  };

  return (
    <React.Fragment>
      <Hidden only={["xs", "sm"]}>
        <Particles
          height={150}
          style={{ backgroundColor: getHeaderBackgroundColor(theme) }}
        />
      </Hidden>

      <Container maxWidth="md" className={classes.container}>
        <SimpleBreadcrumbs
          links={[{ title: "Главная", link: "/" }]}
          current="Новости"
        />
        <br />
        <main>
          <Grid container spacing={2}>
            {getCards()}
          </Grid>
          <Grid
            container
            spacing={0}
            direction="column"
            alignItems="center"
            justify="center"
            style={{ paddingBottom: "20px" }}
          >
            <Grid item sm={12}>
              <IconButton
                onClick={(e) => setPage((page) => page - 1)}
                disabled={
                  data === null ||
                  data === undefined ||
                  data.length === 0 ||
                  page === 1
                }
              >
                <ArrowBackIcon
                  style={{
                    color:
                      page === 1
                        ? theme.palette.primary.light
                        : theme.palette.primary.contrastText,
                  }}
                />
              </IconButton>
              {page}
              <IconButton
                onClick={(e) => setPage((page) => page + 1)}
                disabled={
                  data === null ||
                  data === undefined ||
                  data.length === 0 ||
                  data.length < 9 ||
                  Math.ceil(data.length / 9) <= page
                }
              >
                <ArrowForwardIcon
                  style={{
                    color: "white",
                  }}
                />
              </IconButton>
            </Grid>
          </Grid>
        </main>
      </Container>
      <Footer />
    </React.Fragment>
  );
}
export default News;
